'use client'
import { Button } from "@/components/ui/Button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { useState, useEffect } from "react";

export default function CreateDepartment() {
  const [name, setName] = useState("");
  const [code, setCode] = useState("");
  const [facultyId, setFacultyId] = useState("");
  const [faculties, setFaculties] = useState([]);

  useEffect(() => {
    // Fetch faculties from backend
    setFaculties([
      { _id: "1", name: "Engineering" },
      { _id: "2", name: "Science" },
    ]);
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log({ name, code, facultyId });
    alert(`Department "${name}" created under faculty "${faculties.find(f => f._id === facultyId)?.name}"`);
    setName(""); setCode(""); setFacultyId("");
  };

  return (
    <div className="p-6">
      <h2 className="text-xl font-bold mb-4">Create New Department</h2>
      <form onSubmit={handleSubmit} className="space-y-4">
        <input
          type="text"
          placeholder="Department Name"
          value={name}
          onChange={(e) => setName(e.target.value)}
          className="border p-2 w-full"
          required
        />
        <input
          type="text"
          placeholder="Department Code"
          value={code}
          onChange={(e) => setCode(e.target.value)}
          className="border p-2 w-full"
          required
        />


                    <Select onValueChange={()=>{}}>
                      <SelectTrigger className="w-full sm:w-80">
                        <SelectValue placeholder="Choose a course" />
                      </SelectTrigger>
                      <SelectContent>
                        {faculties.map((c) => (
                          <SelectItem key={c.id} value={c.id}>
                            {c.name}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
        <Button type="submit" >
          Create Department
        </Button>
      </form>
    </div>
  );
}
